.cat_new_carosal .owl-nav .owl-next {
    position: absolute;
    content: '';
    top: 50%;
    right: -5%;
}

.cat_new_carosal .owl-nav .owl-prev {
    position: absolute;
    content: '';
    top: 50%;
    left: -5%;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin: 0;
    margin-top: -15px;
    color: white;
    font-size: 16px;
    line-height: 1;
    padding: 0;
    border: 0.1rem solid #ebebeb;
    background: var(--base-color) !important;
    cursor: pointer;
    border-radius: 50px;
    transition: all .3s ease;
}

.cat_new_carosal button.owl-prev.disabled span,
button.owl-next span {
    color: #000;
    background: var(--base-color) !important;
}

.cat_new_carosal button.owl-prev.disabled {
    position: absolute;
    top: 50%;
    left: -5%;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin: 0;
    margin-top: -15px;
    color: #2b579a;
    font-size: 16px;
    line-height: 1;
    padding: 0;
    border: 0.1rem solid #ebebeb;
    background: var(--base-color) !important;
    cursor: pointer;
    border-radius: 50px;
    transition: all .3s ease;
}

.cat_new_carosal button.owl-next {

    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    margin: 0;
    margin-top: -15px;
    color: #2b579a;
    font-size: 16px;
    line-height: 1;
    padding: 0;
    border: 0.1rem solid #ebebeb;
    background: var(--base-color) !important;
    cursor: pointer;
    border-radius: 50px;
    transition: all .3s ease;
}

.owl-theme .owl-nav .owl-prev i::before,
.owl-theme .owl-nav .owl-next i::before {
    margin: 0;
}

.owl-theme .owl-nav .owl-prev:not(.disabled):hover,
.owl-theme .owl-nav .owl-prev:not(.disabled):active .owl-theme .owl-nav .owl-prev:not(.disabled):focus,
.owl-theme .owl-nav .owl-next:not(.disabled):hover,
.owl-theme .owl-nav .owl-prev:not(.disabled):active .owl-theme .owl-nav .owl-next:not(.disabled):focus,
.owl-theme .owl-nav .owl-next:not(.disabled):active .owl-theme .owl-nav .owl-prev:not(.disabled):focus,
.owl-theme .owl-nav .owl-next:not(.disabled):active .owl-theme .owl-nav .owl-next:not(.disabled):focus {
    background: var(--base-color) !important;
    border: none !important;
    color: #fff !important;
}

.owl-theme .owl-nav .owl-prev.disabled,
.owl-theme .owl-nav .owl-next.disabled {
    color: #ccc !important;
    background: var(--base-color) !important;
}

.owl-theme .owl-nav .owl-prev {
    left: -60px !important;
    top: -200px !important;
}

.owl-theme .owl-nav .owl-next {
    right: -60px !important;
    top: -200px !important;
}

.owl-theme .owl-dots .owl-dot.active span {
    background-color: #4dae67;
    border-color: #4dae67;
}

.owl-theme .owl-dots .owl-dot span {
    margin: 5px;
}

@media (max-width: 1300px) {
    .owl-theme .owl-nav .owl-prev {
        left: 20px !important;
    }

    .owl-theme .owl-nav .owl-next {
        right: 20px !important;
    }
}

.owl-nav-full .owl-nav {
    opacity: 0;
    transition: opacity 0.4s;
}

.owl-nav-full .owl-nav .owl-prev,
.owl-nav-full .owl-nav .owl-next {
    margin: 0;
    top: 0;
    height: 100%;
    transform: none;
    border: 0;
    font-size: 3rem;
    font-weight: 400;
    color: #333;
    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.8);
    transition: 0.4s;
}

.owl-nav-full .owl-nav .owl-prev:not(.disabled):hover,
.owl-nav-full .owl-nav .owl-next:not(.disabled):hover {
    color: #4dae67;
    background-color: rgba(255, 255, 255, 0.8);
}

.owl-nav-full .owl-nav .owl-prev.disabled,
.owl-nav-full .owl-nav .owl-next.disabled {
    visibility: hidden;
    opacity: 0;
}

.owl-nav-full .owl-nav .owl-prev {
    left: 0;
    box-shadow: 4px 0px 7px -5px rgba(0, 0, 0, 0.2);
}

.owl-nav-full .owl-nav .owl-next {
    right: 0;
    box-shadow: -4px 0px 7px -5px rgba(0, 0, 0, 0.2);
}

.owl-nav-full:hover .owl-nav {
    opacity: 1;
}

.owl-nav-full:hover .owl-prev {
    left: 0;
}

.owl-nav-full:hover .owl-next {
    right: 0;
}

.owl-nav-arrow .owl-nav .owl-next,
.owl-nav-arrow .owl-nav .owl-prev {
    margin: 0;
    color: #666;
    font-weight: 400;
    width: 1em;
    height: 1em;
    font-size: 4.5rem;
    border: none;
}

.owl-nav-arrow .owl-nav .owl-next i,
.owl-nav-arrow .owl-nav .owl-prev i {
    display: none;
}

.owl-nav-arrow .owl-nav .owl-next::before,
.owl-nav-arrow .owl-nav .owl-prev::before {
    font-family: "riode";
}

.owl-nav-arrow .owl-nav .owl-next:not(.disabled):hover,
.owl-nav-arrow .owl-nav .owl-next:not(.disabled):active,
.owl-nav-arrow .owl-nav .owl-next:not(.disabled):focus,
.owl-nav-arrow .owl-nav .owl-prev:not(.disabled):hover,
.owl-nav-arrow .owl-nav .owl-prev:not(.disabled):active,
.owl-nav-arrow .owl-nav .owl-prev:not(.disabled):focus {
    background-color: transparent;
    color: #4dae67;
}

.owl-nav-arrow .owl-nav .owl-next.disabled,
.owl-nav-arrow .owl-nav .owl-prev.disabled {
    color: #aaa;
}

.owl-nav-arrow .owl-nav .owl-next {
    right: 5%;
}

.owl-nav-arrow .owl-nav .owl-next::before {
    content: "";
}

.owl-nav-arrow .owl-nav .owl-prev {
    left: 5%;
    margin-right: 20px;
}

.owl-nav-arrow .owl-nav .owl-prev::before {
    content: "";
}

.owl-nav-inner .owl-nav .owl-prev,
.owl-nav-inner .owl-nav .owl-next {
    opacity: 0;
    visibility: hidden;
}

.owl-nav-inner .owl-nav .owl-prev {
    left: 0;
}

.owl-nav-inner .owl-nav .owl-next {
    right: 0;
}

.owl-nav-inner:hover .owl-prev,
.owl-nav-inner:hover .owl-next {
    opacity: 1;
    visibility: visible;
}

.owl-nav-inner:hover .owl-prev {
    left: 2rem;
}

.owl-nav-inner:hover .owl-next {
    right: 2rem;
}

.owl-nav-top .owl-nav {
    position: absolute;
    display: flex;
    top: -5.3rem;
    right: -0.4rem;
    color: #ccc;
}

.owl-nav-top .owl-nav .owl-next:not(.disabled) .owl-prev:not(.disabled) {
    color: #777;
}

.owl-nav-top .owl-nav .owl-prev,
.owl-nav-top .owl-nav .owl-next {
    position: static;
    transform: translateY(-11%);
}

.owl-nav-top .owl-nav .owl-next {
    margin-right: 5px;
}

.owl-nav-top .owl-nav i {
    padding: 0.1rem 0.5rem;
    font-size: 1.8rem;
    font-weight: 700;
}

.owl-nav-top .owl-nav i::before {
    margin: 0;
}

.owl-nav-top.owl-nav-arrow .owl-nav .owl-prev,
.owl-nav-top.owl-nav-arrow .owl-nav .owl-next {
    font-size: 3.4rem;
}

.owl-nav-bottom .owl-nav {
    position: absolute;
    bottom: 1.7rem;
    right: -0.4rem;
    color: #999;
}

.owl-nav-bottom .owl-nav i {
    font-size: 2rem;
    font-weight: 700;
}

.owl-nav-bottom .owl-nav i::before {
    margin: 0;
}

.owl-nav-bottom .owl-prev {
    margin-right: 0.8rem;
}

.owl-dot-white .owl-dots .owl-dot span {
    background-color: #fff;
    border-color: #fff;
    opacity: 0.8;
}

.owl-dot-white .owl-dots .owl-dot.active span {
    background-color: #fff;
    border-color: #fff;
    opacity: 1;
}

.owl-dot-grey .owl-dots .owl-dot span {
    background-color: #efefef;
    border-color: #efefef;
}

.owl-dot-grey .owl-dots .owl-dot.active span {
    background-color: #999;
    border-color: #999;
}

.owl-dot-dark .owl-dots .owl-dot span {
    background-color: #a1a1a1;
    border-color: #a1a1a1;
}

.owl-dot-dark .owl-dots .owl-dot.active span {
    background-color: #333;
    border-color: #333;
}

.owl-dot-inner .owl-dots {
    position: absolute;
    bottom: 4.5rem;
    left: 50%;
    transform: translateX(-50%);
}

.brand-carousel .owl-stage {
    display: flex;
    align-items: center;
}

.brand-carousel .owl-item img {
    width: auto;
    margin: auto;
}

.brand-carousel:not(.loaded) {
    text-align: center;
    align-items: center;
}

.owl-carousel .slide-animate {
    transform: translate3d(0, 0, 0) scale(1);
    will-change: filter, transform, opacity;
    visibility: hidden;
    opacity: 0;
}

.owl-carousel .slide-animate.show-content {
    visibility: visible;
    opacity: 1;
}

.owl-full-height,
.owl-full-height .owl-stage-outer,
.owl-full-height .owl-stage,
.owl-full-height .owl-item,
.owl-same-height .owl-item>* {
    height: 100%;
}

.owl-same-height .owl-stage {
    display: flex;
}

.owl-full-height .owl-item img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.owl-shadow-carousel .owl-stage-outer {
    margin: -30px;
    padding: 30px;
}

.owl-nav-bg .owl-nav .owl-prev {
    left: 15.4%;
}

.owl-nav-bg .owl-nav .owl-next {
    right: 15.4%;
}

.owl-nav-bg .owl-nav [class*="owl-"] {
    width: 4.8rem;
    height: 4.8rem;
    border-width: 2px;
    font-size: 3.2rem;
    color: #666;
    border-color: #666;
}

.owl-nav-bg .owl-nav [class*="owl-"].disabled {
    border-color: #acabab;
}

.owl-nav-bg .owl-nav i {
    line-height: 1;
    margin-bottom: 2px;
}

.owl-nav-fade .owl-nav .owl-prev {
    opacity: 0;
    margin-left: 4rem;
}

.owl-nav-fade .owl-nav .owl-next {
    opacity: 0;
    margin-right: 4rem;
}

.owl-nav-fade:hover .owl-nav .owl-prev {
    opacity: 1;
    margin-left: 0;
}

.owl-nav-fade:hover .owl-nav .owl-next {
    opacity: 1;
    margin-right: 0;
}

.owl-split .owl-item:not(:last-child) {
    position: relative;
}

.owl-split .owl-item:not(:last-child)::after {
    content: "";
    position: absolute;
    left: 100%;
    top: 0;
    bottom: 0;
    width: 1px;
    background-color: #ebebeb;
}

.owl-middle .owl-stage {
    display: flex;
    align-items: center;
}

.rotate-slider {
    padding-left: 17.9%;
}

.rotate-slider .owl-stage-outer {
    margin: -20px;
    padding: 20px;
    padding-left: 23%;
    margin-left: -23%;
}

.rotate-slider .owl-item {
    transition: 1s;
    box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.1);
}

.rotate-slider .owl-item:not(.active) {
    transform: scale(0.8) translateX(100%);
    transform-origin: left;
}

.rotate-slider .owl-nav .owl-next {
    right: auto;
    left: 40px;
    font-size: 36px;
}

.rotate-slider .owl-nav .owl-next::before {
    content: "";
}

.rotate-slider .owl-nav .owl-prev {
    display: none;
}

@media (max-width: 479px) {
    .rotate-slider .owl-nav .owl-next {
        right: auto;
        left: 15px;
        font-size: 24px;
    }
}

