/* 4. base */
/* -------------------------------------------
    Base
---------------------------------------------- */
*,
::after,
::before {
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
    font-size-adjust: 100%;
    font-weight: 400;
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html.dark-theme {
    background-color: #151515;
}

.riode-rounded-skin {
    margin: 0;
    overflow-x: hidden;
    font-family: var(--font-monda);
    font-size: 1.4rem;
    line-height: 1.6;
    color: #666;
}

.description-title {
    font-size: 32px;
    line-height: 1.09375;
    color: var(--color-text);
    font-weight: 100 !important;
    font-family: var(--font-monda);
    margin-bottom: 40px !important;
}

main {
    display: block;
    position: relative;
    background-color: #fff;
}

.riode-rounded-skin:not(.loaded) * {
    transition: all 0s !important;
}

.page-wrapper {
    position: relative;
    transition: margin 0.4s;
}

table {
    width: 100%;
    border-collapse: collapse;
}

table td,
table th {
    padding: 0;
}

.section {
    padding: 7rem 0;
}

.grey-section {
    background: #f6f7f9;
}

.background-section {
    background-repeat: no-repeat;
}

.parallax {
    background-color: #3c3f41;
}

ul {
    padding-left: 1.5em;
}

.menu,
.menu ul,
.mobile-menu,
.mobile-menu ul,
.nav,
.nav ul,
.widget-body,
.widget-body ul,
.list,
.breadcrumb,
.filter-items,
.select-menu>ul,
.dropdown-box,
.pagination,
.nav-filters,
.category ul,
.comments ul,
.product-nav,
.product-tabs>div ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
    transition: color 0.3s;
}

a:hover {
    color: #4dae67;
}

:focus {
    outline: 0;
}

figure {
    margin: 0;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

hr {
    margin-bottom: 2rem;
    border: 0;
    border-top: 1px solid #eee;
}

input {
    /* -webkit-appearance: none; */
    border: 0;
    padding: 0;
    font-family: var(--font-monda);
}

input:focus {
    outline: 0;
}

.input-wrapper>input,
.input-wrapper>button,
.input-group>input,
.input-group>button {
    margin: 0px;
}

i {
    font-style: normal;
}

button:focus {
    outline: none;
}

.scrollable,
.sidebar-content {
    -webkit-overflow-scrolling: touch;
}

.scrollable::-webkit-scrollbar,
.sidebar-content::-webkit-scrollbar {
    height: 7px;
    width: 4px;
}

.scrollable::-webkit-scrollbar-thumb,
.sidebar-content::-webkit-scrollbar-thumb {
    margin-right: 2px;
    background: rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    cursor: pointer;
}

.scrollable-light::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
}

@keyframes bouncedelay {

    0%,
    80%,
    100% {
        transform: scale(1);
    }

    40% {
        transform: scale(1.2);
    }
}

@keyframes rotatedelay {
    0% {
        transform: rotateZ(0);
    }

    100% {
        transform: rotateZ(360deg);
    }
}

@keyframes reveal-1 {
    0% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }

    20% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(55%, 55%, 0);
        border-radius: 0;
    }

    80% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(55%, 55%, 0);
        border-radius: 0;
    }

    100% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }
}

@keyframes reveal-2 {
    0% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }

    20% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(55%, -55%, 0);
        border-radius: 0;
    }

    80% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(55%, -55%, 0);
        border-radius: 0;
    }

    100% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }
}

@keyframes reveal-3 {
    0% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }

    20% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(-55%, -55%, 0);
        border-radius: 0;
    }

    80% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(-55%, -55%, 0);
        border-radius: 0;
    }

    100% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }
}

@keyframes reveal-4 {
    0% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }

    20% {
        transform: rotate3d(0, 0, 1, 135deg) translate3d(-55%, 55%, 0);
        border-radius: 0;
    }

    80% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(-55%, 55%, 0);
        border-radius: 0;
    }

    100% {
        transform: rotate3d(0, 0, 1, 495deg) translate3d(0, 0, 0);
        border-radius: 50%;
    }
}

.bounce-loader {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    transition: all 0.2s;
    text-align: center;
    z-index: 10000;
}

.bounce-loader .bounce1,
.bounce-loader .bounce2,
.bounce-loader .bounce3,
.bounce-loader .bounce4 {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    margin-bottom: 5px;
    background-color: #4dae67;
}

.bounce-loader .bounce1 {
    animation: 2s infinite reveal-1;
}

.bounce-loader .bounce2 {
    animation: 2s infinite reveal-2;
}

.bounce-loader .bounce3 {
    animation: 2s infinite reveal-3;
}

.bounce-loader .bounce4 {
    animation: 2s infinite reveal-4;
}

.bounce-loader .bounce3 {
    border: 3px solid #4dae67;
    background-color: transparent;
}

.appear-animate {
    transform: translate3d(0, 0, 0) scale(1);
    will-change: transform, filter, opacity;
}

.fade {
    opacity: 0;
    transition: opacity 0.5s;
}

.fade.in {
    opacity: 1;
}

.d-icon-arrow-up:before {
    content: "";
    font-size: 20px;
    top: -10px;
    bottom: 0;
    right: 0px;
