.widget-title {
    margin-bottom: 2rem;
    padding: 1.1rem 0;
    font-size: 1.6rem;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    border-bottom: 1px solid #eee;
    letter-spacing: -0.025em;
}

.widget-products .widget-title {
    padding: 2.8rem 0.3rem 1.6rem;
    font-size: 1.8rem;
    font-weight: 600;
    letter-spacing: -0.03em;
}

/* -------------------------------------------
    Form
---------------------------------------------- */
.form .form-control {
    margin-bottom: 2rem;
}

.form-control {
    display: block;
    width: 100%;
    padding: 0.85rem 2rem;
    border: 1px solid #e3e3e3;
    font-size: 1.4rem;
    line-height: 1.5;
    font-weight: 400;
    color: #666;
    background-color: transparent;
    border-radius: 0;
    box-shadow: none;
    outline: 0;
}

.form-control::placeholder {
    color: inherit;
    transition: color 0.3s;
}

.form-control:focus::placeholder {
    color: transparent;
}

.form-control:focus::-webkit-input-placeholder {
    color: transparent;
}

.form-control:focus::-moz-placeholder {
    color: transparent;
}

.form-control.form-solid {
    background-color: #fff;
    border: 0;
    color: #8d8d8d;
}

textarea {
    font-family: inherit;
    min-width: 100%;
    max-width: 100%;
}

.custom-checkbox {
    position: absolute;
    opacity: 0;
}

.custom-checkbox+label {
    display: block;
    position: relative;
    padding-left: 2.4rem;
    cursor: pointer;
}

.custom-checkbox+label::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 18px;
    height: 18px;
    line-height: 17px;
    border: 1px solid #ccc;
    border-radius: 2px;
}

