  Tooltips 
------------------------------------------- */
.tooltiptext {
    font-family: var(--font-monda);
    position: absolute;
    padding: 1rem 0.7rem;
    bottom: 150%;
    left: 48%;
    background-color: #333;
    color: #fff;
    font-size: 1.3rem;
    font-weight: 400;
    letter-spacing: 0.01em;
    text-align: center;
    visibility: hidden;
    opacity: 0;
    transform: translateX(-50%);
    z-index: 1;
    transition: opacity 0.3s;
}

.tooltip-top::after,
.tooltip-bottom::after {
    content: "";
    position: absolute;
    left: 50%;
    margin-left: -6px;
    border-width: 6px;
    border-style: solid;
}

.tooltip-top::after {
    top: 96%;
    border-color: #333 transparent transparent transparent;
}

.tooltip-bottom::after {
    top: -37%;
    border-color: transparent transparent #333 transparent;
}

/* -------------------------------------------
    Buttons
        - Different Backgrounds
        - Outline Buttons
        - Gradient Buttons
        - Simple Link Buttons
        - Button Sizes
        - Shapes & Shadow
        - With Icon & Animation
        - On Background
---------------------------------------------- */
.btn {
    display: inline-block;
    border: 2px solid #e4eaec;
    background-color: var(--base-color);
    color: #000;
    outline: 0;
    padding: 1.22em 2.78em;
    font-weight: 100;
    font-size: 1.4rem;
    font-family: var(--font-monda);
    letter-spacing: 0;
    line-height: 1.2;
    text-transform: uppercase;
    text-align: center;
    transition: color 0.3s, border-color 0.3s, background-color 0.3s, box-shadow 0.3s;
    white-space: nowrap;
    cursor: pointer;
}

.btn:hover,
.btn:active,
.btn:focus {
    color: #222;
    border-color: #cfdadd;
    background-color: #cfdadd;
}

.btn i {
    display: inline-block;
    vertical-align: middle;
    margin-left: 0.4rem;
    line-height: 0;
    font-size: 1.9rem;
}

.btn i::before {
    margin: 0;
}

.btn-solid {
    background-color: #fff;
    border-color: #fff;
    color: #222;
}

.btn-solid:hover,
.btn-solid:active,
.btn-solid:focus {
    border-color: #222;
    background-color: #222;
    color: #fff;
}

.btn-outline {
    border: 2px solid #e4eaec;
    color: #666;
    background-color: transparent;
}

.btn-outline:hover,
.btn-outline:active,
.btn-outline:focus {
    border-color: #666;
    background-color: #666;
    color: #fff;
}

.btn-outline.btn-outline-light {
    padding: 1.07em 2.21em;
    border-width: 1px;
}

.btn-outline.btn-bg {
    color: #fff;
}

.btn-gradient {
    position: relative;
    color: #fff;
    border: none;
    overflow: hidden;
    transition: background 0.25s, color 0.25s, border 0.25s;
}

.btn-gradient::after {
    content: "";
    position: absolute;
    top: -50%;
    left: 0;
    right: 0;
    bottom: -50%;
    background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 20%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0) 80%);
    transform: skewX(-45deg) translateX(-100%);
    transition: transform 0.8s;
}

.btn-gradient:hover::after {
    transform: skewX(-45deg) translateX(100%);
}

.btn-gradient.btn-primary {
    background: linear-gradient(135deg, #3b8df1, #5449ff);
}

.btn-gradient.btn-secondary {
    background: linear-gradient(135deg, #ffa35f, #ef5454);
}

.btn-gradient.btn-alert {
    background: linear-gradient(135deg, #f85283, #b33c9e);
}

.btn-gradient.btn-success {
    background: linear-gradient(135deg, #a0ec69, #87cf47);
}

.btn-gradient.btn-dark {
    background: linear-gradient(135deg, #666, #222);
}

.btn-icon-left i,
.btn-icon-right i,
.btn-reveal-left i,
.btn-reveal-right i {
    line-height: 0;
}

.btn-icon-right {
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.btn-icon-right i {
    margin-left: 0.7rem;
}

.btn-icon-left {
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.btn-icon-left i {
    margin-right: 0.7rem;
    margin-left: 0;
}

.btn-reveal-left i {
    opacity: 0;
    margin-left: -1em;
    transform: translateX(0.5em);
    margin-right: 0;
    transition: transform 0.3s, opacity 0.3s, margin 0.3s;
}

.btn-reveal-left:hover i,
.btn-reveal-left:active i,
.btn-reveal-left:focus i {
    opacity: 1;
    margin-left: 0;
    transform: translateX(0);
    margin-right: 0.7rem;
}

.btn-reveal-right i {
    opacity: 0;
    margin-right: -1em;
    transform: translateX(-0.5em);
    margin-left: 0;
    transition: transform 0.3s, opacity 0.3s, margin 0.3s;
}

.btn-reveal-right:hover i,
.btn-reveal-right:active i,
.btn-reveal-right:focus i {
    opacity: 1;
    margin-left: 0.7rem;
    transform: translateX(0);
    margin-right: 0;
}

.btn-slide-left:hover i,
.btn-slide-left:active i,
.btn-slide-left:focus i {
    animation: 0.5s ease slideLeft;
}

.btn-slide-right:hover i,
.btn-slide-right:active i,
.btn-slide-right:focus i {
    animation: 0.5s ease slideRight;
}

.btn-slide-up:hover i,
.btn-slide-up:active i,
.btn-slide-up:focus i {
    animation: 0.5s ease slideUp;
}

.btn-slide-down:hover i,
.btn-slide-down:active i,
.btn-slide-down:focus i {
    animation: 0.5s ease slideDown;
}

.btn-infinite:hover i {
    animation-iteration-count: infinite;
}

@keyframes slideLeft {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-0.5em);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes slideRight {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(0.5em);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes slideUp {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-0.5em);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes slideDown {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(0.5em);
    }

    100% {
        transform: translateY(0);
    }
}

