.overlay,
.overlay-visible {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    color: #fff;
    transition: padding 0.3s ease-out, background-color 0.3s, opacity 0.3s;
}

.overlay {
    padding-top: 10rem;
    background: rgba(0, 0, 0, 0.3);
    opacity: 0;
}

a>.overlay {
    cursor: pointer;
}

figure:hover .overlay {
