.instagram {
    position: relative;
    overflow: hidden;
    border-radius: 0.3rem;
}

.instagram a {
    display: block;
    position: relative;
    height: 100%;
}

.instagram a::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 35px;
    bottom: 48px;
    background: #e7f2f9;
    opacity: 1;
    transition: opacity 0.3s;
    z-index: -1;
    border-radius: 10px;
}

.instagram a::after {
    /* content: ""; */
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(90deg) scale(2);
    font-size: 3rem;
    font-family: "riode";
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s, transform 0.3s;
    z-index: 1;
}

.instagram img {
    /* display: block;
    transition: transform 1s;
    width: 100%;
    height: auto; */
}

figure.instagram img:hover {
    transform: translateY(-2.1rem);
    transition: transform 1s;
}

.instagram:hover a::before {
    opacity: 0.5;
}

.instagram:hover a::after {
    opacity: 1;
    transform: translate(-50%, -50%) rotate(0deg) scale(1);
}

